.page-header {
  padding-top: 35px;
  padding-right: 25px;
  padding-left: 25px;

  background-color: #1b2d37;
  background-image: url("#{$image-dir}/background-pattern.png");
  background-repeat: no-repeat;

  overflow: hidden;

  @media (max-width: $tablet-width) {
    padding-top: 19px;
  }

  @media (max-width: $mobile-width) {
    padding-top: 15px;
    padding-right: 15px;
    padding-left: 15px;
  }
}

.page-header__wrapper {
  @include centerer();

  position: relative;

  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 230px;

  @media (max-width: $tablet-width) {
    margin-bottom: 117px;
  }

  @media (max-width: $mobile-width) {
    margin-bottom: 230px;
  }
}

.page-header__navigation {
  position: relative;

  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  width: 30%;
  max-width: 340px;
  min-width: 300px;


  @media (max-width: $tablet-width) {
    justify-content: space-between;
    flex-grow: 1;
    width: auto;
    max-width: none;
    min-width: auto;
    margin-right: 49px;
  }

  @media (max-width: $mobile-width) {
    margin-right: 40px;
  }
}

.page-header__logo {
  position: relative;

  display: flex;
  flex-wrap: wrap;
}

.page-header__logo-image {
  width: 52px;
  height: 52px;
  margin-right: 14px;

  @media (max-width: $tablet-width) {
    margin-right: 16px;
  }

  @media (max-width: $mobile-width) {
    width: 38px;
    height: 38px;
  }
}

.page-header__logo-wrapper {
  position: relative;

  display: flex;
  flex-flow: column;

  @media (max-width: $tablet-width) {
    padding-top: 7px;
  }

  @media (max-width: $mobile-width) {
    display: none;
  }
}

.page-header__logo-head {
  font-weight: 700;
  font-size: 25px;
  line-height: 1.2;
  font-family: $robotocondensed-font;
  color: $font-color--primary;
}

.page-header__logo-caption {
  font-weight: 500;
  font-size: 12px;
  line-height: 1.2;
  font-family: $roboto-font;
  color: #788891;
}

.page-header__menu-button {
  position: relative;

  width: 37px;
  height: 40px;

  background-color: transparent;
  border: none;

  &::before {
    content: "";
    position: absolute;

    top: 12px;
    left: 15px;
    width: 100%;
    height: 1px;

    background-color: $font-color--primary;
    box-shadow: 0 11px 0 0 $font-color--primary;
  }
}

.page-header__contacts {
  position: relative;

  display: flex;
  align-items: center;
  width: 60%;
  max-width: 650px;

  @media (max-width: $tablet-width) {
    display: flex;
    justify-content: space-between;
    width: 85px;
    max-width: none;
  }

  @media (max-width: $mobile-width) {
    width: 75px;
  }
}

.page-header__email {
  padding-left: 29px;

  @media (max-width: $tablet-width) {
    padding-left: 0;
  }

  a {
    position: relative;

    font-weight: 400;
    font-size: 16px;
    line-height: 1.2;
    font-family: $brand-font;
    color: $font-color--primary;

    cursor: pointer;

    @media (max-width: $tablet-width) {
      display: block;
      width: 25px;
      height: 18px;

      font-size: 0;
    }

    &::before {
      content: "";
      position: absolute;

      left: -39px;
      width: 19px;
      height: 14px;

      background-image: url("#{$image-dir}/envelope.svg");
      background-repeat: no-repeat;

      @media (max-width: $tablet-width) {
        left: 0;

        width: 100%;
        height: 100%;

        background-size: 25px;
      }
    }
  }
}

.page-header__phone {
  margin-left: auto;
  padding-left: 38px;

  @media (max-width: $tablet-width) {
    margin-left: 0;
    padding-left: 0;
  }


  a {
    position: relative;

    font-weight: 400;
    font-size: 16px;
    line-height: 1.2;
    font-family: $brand-font;
    color: $font-color--primary;

    cursor: pointer;

    @media (max-width: $tablet-width) {
      display: block;
      width: 24px;
      height: 24px;

      font-size: 0;
    }

    &::before {
      content: "";
      position: absolute;
      left: -36px;

      width: 18px;
      height: 18px;

      background-image: url("#{$image-dir}/phone.svg");
      background-repeat: no-repeat;

      @media (max-width: $tablet-width) {
        left: 0;

        width: 100%;
        height: 100%;

        background-size: 24px;
      }
    }
  }
}

.page-header__order-call {
  margin-left: 46px;
  padding: 14px 28px;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.2;
  font-family: $robotocondensed-font;
  letter-spacing: 0.05em;
  text-align: center;
  text-transform: uppercase;
  color: $font-color--primary;

  cursor: pointer;

  background-color: transparent;
  border: 1px solid #ffffff;
  border-radius: 5px;

  @media (max-width: $tablet-width) {
    display: none;
  }
}

.page-header__profile-info {
  @include centerer();

  position: relative;
}

.page-header__profile-info-wrapper {
  position: relative;
  z-index: 1;

  @media (max-width: $tablet-width) {
    padding-left: 65px;
  }

  @media (max-width: $mobile-width) {
    padding-left: 0;
  }
}

.page-header__profile {
  margin-bottom: 25px;

  font-weight: 700;
  font-size: 80px;
  line-height: 1.2;
  font-family: $robotocondensed-font;
  color: $font-color--primary;
  letter-spacing: 0.06em;
  text-transform: uppercase;

  @media (max-width: $tablet-width) {
    margin-bottom: 27px;
    padding-right: 171px;

    font-size: 60px;
  }

  @media (max-width: $mobile-width) {
    font-size: 40px;
  }
}

.page-header__profile-caption {
  position: relative;

  margin-bottom: 44px;
  padding-left: 94px;
  padding-right: 620px;

  font-weight: 400;
  font-size: 22px;
  line-height: 1.5;
  font-family: $brand-font;
  color: $font-color--primary;

  @media (max-width: $tablet-width) {
    max-width: 500px;
    margin-bottom: 55px;
    padding-right: 60px;
    padding-left: 49px;

    font-size: 18px;
  }

  @media (max-width: $mobile-width) {
    margin-bottom: 42px;
    padding-left: 51px;
    padding-right: 0;

    font-size: 16px;
  }

  &::before {
    content: "";
    position: absolute;
    top: 14px;
    left: 2px;

    width: 66px;
    height: 2px;

    background-color: #ffffff;

    @media (max-width: $tablet-width) {
      width: 33px;
    }

    @media (max-width: $mobile-width) {
      width: 33px;
    }
  }
}

.page-header__profile-button {
  margin-bottom: 290px;
  margin-left: 90px;
  padding: 18px 46px;

  font-weight: 700;
  font-size: 16px;
  line-height: 1.2;
  font-family: $robotocondensed-font;
  color: $font-color--primary;
  letter-spacing: 0.05em;
  text-align: center;
  text-transform: uppercase;

  cursor: pointer;

  background-color: $brand-color;
  border-radius: 5px;
  border: none;

  @media (max-width: $tablet-width) {
    margin-bottom: 160px;
    margin-left: 49px;
    padding: 17px 31px;

    font-size: 16px;
  }

  @media (max-width: $mobile-width) {
    display: none;
  }

  &--mobile {
    display: none;

    @media (max-width: $mobile-width) {
      display: block;
      width: 100%;
      margin-left: 0;
      margin-bottom: 70px;
      padding: 18px 30px;
    }
  }
}

.page-header__profile-image-wrapper {
  position: absolute;
  top: -60px;
  right: -78px;

  @media (max-width: $tablet-width) {
    top: -75px;
    right: -230px;
  }

  @media (max-width: $mobile-width) {
    top: -195px;
    right: -20px;
  }
}

.page-header__profile-image {
  @media (max-width: $tablet-width) {
    width: 465px;
    height: 340px;
  }

  @media (max-width: $mobile-width) {
    width: 300px;
    height: 220px;
  }
}
